.people-profile-listing::after {
  clear: both;
}

.sidebar {
  font-family: "Helvetica Neue", Helvetica, sans-serif;
  font-size: 14px;
  font-size: 0.875rem;
  color: #666;
}

.block--sidebar_first {
  border-bottom: 8px #f2f2f2 solid;
}

.block--sidebar_first:last-of-type {
  border-bottom: none;
}

.slideshow-wrapper {
  position: relative;
  max-width: 1260px;
}

.slideshow img {
  height: auto;
}

.slide {
  line-height: 1;
  position: relative;
}

.slideshow-pager {
  position: absolute;
  bottom: 2rem;
  right: 2rem;
  z-index: 10;
  line-height: 1;
}

.slideshow-pager a {
  display: inline-block;
  font-size: 16px;
  font-size: 1rem;
  color: #bbb;
  color: rgba(255, 255, 255, 0.4);
  margin: auto 0.25rem;
  border-bottom: none;
}

.slideshow-pager a.activeSlide {
  color: #fff;
}

.slide__caption {
  position: absolute;
  bottom: 3rem;
  right: 2rem;
  display: inline-block;
  padding: 0.25rem 0.75rem;
  margin: 0;
  font-family: "Helvetica Neue", Helvetica, sans-serif;
  font-size: 14px;
  font-size: 0.875rem;
  line-height: 1.3;
  font-weight: 300;
  font-style: italic;
  color: #fff;
  background-color: rgba(0, 0, 0, 0.5);
  max-width: 75%;
}

@media (min-width: 48.0625em) {
  .column-left,
  .column-right {
    display: inline-block;
    width: 49%;
    vertical-align: top;
  }
}

@media (min-width: 48.0625em) {
  .column-left {
    padding-right: 2.5%;
  }
}

@media (min-width: 48.0625em) {
  .column-right {
